import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import PrivateRoute from 'components/routes/PrivateRoute';

import Home from 'containers/pages/Home';
import Login from 'containers/pages/Login';
import PacienteList from 'containers/pages/pacientes/PacienteList';
import BuscarPaciente from 'containers/pages/pacientes/BuscarPaciente';
import ReportePaciente from 'containers/pages/reportes/ReportePaciente';
import PrediccionPaciente from 'containers/pages/pacientes/PrediccionPaciente';
import Diagnostico from 'containers/pages/pacientes/Diagnostico';
import Error404 from 'containers/errors/Error404';

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/login" element={<Login />} />
        
        {/* ✅ RUTAS PROTEGIDAS */}
        <Route path="/" element={<PrivateRoute><Home /></PrivateRoute>} />
        <Route path="/pacientes" element={<PrivateRoute><PacienteList /></PrivateRoute>} />
        <Route path="/pacientes/buscar" element={<PrivateRoute><BuscarPaciente /></PrivateRoute>} />
        <Route path="/reportes" element={<PrivateRoute><ReportePaciente /></PrivateRoute>} />
        <Route path="/pacientes/prediccion" element={<PrivateRoute><PrediccionPaciente /></PrivateRoute>} />
        <Route path="/diagnostico" element={<PrivateRoute><Diagnostico /></PrivateRoute>} />
        
        <Route path="*" element={<Error404 />} />
      </Routes>
    </Router>
  );
}

export default App;