import { useState } from "react";
import axios from "axios";

const PacienteList = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [pacientes, setPacientes] = useState([]);

  const buscarPacientes = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/api/pacientes/buscar/?search=${searchTerm}`,
        {
          headers: {
            Authorization: `JWT ${localStorage.getItem("token")}`,
          },
        }
      );
      setPacientes(res.data);
    } catch (error) {
      console.error("Error al buscar pacientes:", error);
    }
  };

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Registro de pacientes</h1>

      <div className="flex gap-2 mb-6">
        <input
          type="text"
          placeholder="Buscar por DNI, nombre o apellido"
          className="border px-4 py-2 rounded w-full"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <button
          onClick={buscarPacientes}
          className="bg-blue-600 text-white px-4 py-2 rounded"
        >
          Buscar
        </button>
      </div>

      <div className="grid gap-4">
        {pacientes.length === 0 && (
          <p className="text-gray-500">No hay resultados</p>
        )}

        {pacientes.map((paciente) => (
          <div
            key={paciente.id}
            className="border p-4 rounded shadow bg-white"
          >
            <p className="font-semibold">{paciente.nombre} {paciente.apellido}</p>
            <p>DNI: {paciente.dni}</p>
            <p>Resultado: {paciente.resultado}</p>
            <p>Severidad: {paciente.severidad}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default PacienteList;