import React, { useState } from "react";
import axios from "axios";

const Diagnostico = () => {
  const [imagen, setImagen] = useState(null);
  const [resultado, setResultado] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleImageChange = (e) => {
    setImagen(e.target.files[0]);
    setResultado(null);
    setError(null);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!imagen) return;

    const formData = new FormData();
    formData.append("imagen", imagen);

    setLoading(true);
    setError(null);

    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API_URL}/api/pacientes/prediccion/`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setResultado(res.data);
    } catch (err) {
      setError(err.response?.data?.error || "Error al hacer la predicción");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-xl mx-auto mt-10 p-6 bg-white rounded shadow">
      <h1 className="text-xl font-bold mb-4">Diagnóstico de Retinopatía</h1>

      <form onSubmit={handleSubmit}>
        <input
          type="file"
          accept="image/*"
          onChange={handleImageChange}
          className="mb-4"
        />
        <button
          type="submit"
          disabled={loading}
          className="bg-blue-600 text-white px-4 py-2 rounded"
        >
          {loading ? "Analizando..." : "Enviar Imagen"}
        </button>
      </form>

      {resultado && (
        <div className="mt-6 p-4 bg-green-100 rounded">
          <h2 className="font-semibold text-green-800">Resultado:</h2>
          <p>
            <strong>Clase:</strong> {resultado.prediccion}
          </p>
          <p>
            <strong>Confianza:</strong> {resultado.confianza}
          </p>
        </div>
      )}

      {error && (
        <div className="mt-6 p-4 bg-red-100 rounded text-red-700">
          <strong>Error:</strong> {error}
        </div>
      )}
    </div>
  );
};

export default Diagnostico;