import { useState } from "react";
import axios from "axios";

export default function PrediccionPaciente() {
  const [imagen, setImagen] = useState(null);
  const [resultado, setResultado] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleImageChange = (e) => {
    setImagen(e.target.files[0]);
    setResultado(null); // limpia resultado anterior
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!imagen) return;

    const formData = new FormData();
    formData.append("imagen", imagen);

    setLoading(true);
    try {
      const token = localStorage.getItem("token");
      const res = await axios.post(
        `${process.env.REACT_APP_API_URL}/api/pacientes/prediccion/`,
        formData,
        {
          headers: {
            Authorization: `JWT ${token}`,
            "Content-Type": "multipart/form-data",
          },
        }
      );
      setResultado(res.data);
    } catch (error) {
      alert("Error en la predicción");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-xl mx-auto p-6 bg-white rounded shadow">
      <h2 className="text-2xl font-bold mb-4">Diagnóstico Automático</h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        <input
          type="file"
          accept="image/*"
          onChange={handleImageChange}
          className="block w-full border p-2 rounded"
        />
        <button
          type="submit"
          className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
        >
          {loading ? "Procesando..." : "Diagnosticar"}
        </button>
      </form>

      {resultado && (
        <div className="mt-6 p-4 bg-gray-100 rounded">
          <p><strong>Predicción:</strong> Clase {resultado.prediccion}</p>
          <p><strong>Confianza:</strong> {resultado.confianza * 100}%</p>
        </div>
      )}
    </div>
  );
}